@charset "utf-8";
.exp{
  text-align: center;
  margin-bottom: 50px;
  font-size: 18px;
}


/* Intro 서식 */
.main_wrapper{
  display: flex;
  width: 1200px;
  margin: 0 auto;
}
.main_wrap_left, .main_wrap_right{
  width: 33%;
}
.main_wrap_left span, .main_wrap_right span{
  display: block;
  position: relative;
}

.main_img{
  width: 500px;
  margin: 50px auto;
}
.main_img img{
  width: 100%;
  animation: main_img 3s linear;
}
@keyframes main_img{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

/* 키워드 서식 */
.main_p{
  position: relative;
  animation: shaking 2s infinite;
  text-shadow: 0px 1px 2px #ccc;
  font-size: 18px;
}
.s01{
  top:30px;left: 70px;
  color: rgb(169, 123, 63);
  text-shadow: 0px 1px 2px #a18b8b;
  font-size: 22px;
  animation: shake2 3s infinite;

}
.s02{
  top:100px;left: 250px;
  color: #6F5000;
  font-size: 20px;
  animation: shake 4s infinite;
}
.s03{
  top:300px;left: 320px;
  color: #94762c;
  font-size: 22px;
  animation: shake2 3s infinite;

  
}
.s04{
  color: #CB9304;
  font-size: 20px;
  top:220px;left:100px;
  animation: shake 4s infinite;

}
.s05{
  top: 350px;left:10px;
  color: #9f6a00;
  font-size: 22px;
  text-shadow:0px 1px 2px #5c5656;
  animation: shake2 3s infinite;

}
.s06{
  top: 20px;
  color: #4e3902;
  font-size: 20px;
  animation: shake 4s infinite;

}
.s07{
  top: 360px;left: 200px;
  color: #675425;
  font-size: 22px;
  animation: shake2 3s infinite;

}
@keyframes shake{
  from{
      opacity: 0.8;
  }
  to{
    opacity: 1;
    transform: scale(1.1);
  }
}
@keyframes shake2{
  from{
    opacity: 0.6;
  }
  to{
    opacity: 1;
    transform: scale(1.05);
  }
}
@keyframes shaking{
  0%{
    transform: rotate(5deg);
  }
  50%{
    transform: rotate(-5deg);
  }
  100%{
    transform: rotate(5deg);
  }
}


