/*------ skill bar 서식 ------*/

.skill_bar{
  margin: 50px 0;
}
@keyframes load{
  from {
    width: 0%
  }
}
@-webkit-keyframes load{
  from {
    width: 0%
  }
}
@-moz-keyframes load{
  from {
    width: 0%
  }
}
@-o-keyframes load{
  from {
    width: 0%
  }
}

.bar{
  background-color: #ffffff;
  padding: 1px;
  border-radius: 15px;
  margin-bottom: 5px;
  font-size: 14px;
  color: #5c5656;
  font-weight: bold;
  text-shadow: 1px 1px 1px rgba(254, 254, 254, 0.5);
}

.bar::before{
  content:  attr(data-skill);
  background-color: #ffbb62;
  display: inline-block;
  padding: 1px 0 1px 10px;
  border-radius: inherit;
  animation: load 5s 0s;
  -webkit-animation: load 5s 0s;
  -moz-animation: load 5s 0s;
  -o-animation: load 5s 0s;
}

.bar.back::before{
  background-color: #ffa75f;
}
.bar.middle::before{
  background-color: #ffbb62;
}
.bar.middle2::before{
  background-color: #ffa75f;
}
.bar.middle3::before{
  background-color: #ffbb62;
}
.bar.middle4::before{
  background-color: #ffa75f;
}
.bar.front::before{
  background-color: #ffbb62;
}

.bar.html5::before{
  width: calc(80% - 10px);
}
.bar.css3::before{
  width: calc(80% - 10px);
}
.bar.javascript::before{
  width: calc(75% - 10px);
}
.bar.jquery::before{
  width: calc(70% - 10px);
}
.bar.react::before{
  width: calc(65% - 10px);
}
.bar.sql::before{
  width: calc(65% - 10px);
}
.bar.bootstrap::before{
  width: calc(70% - 10px);
}




/* Contact */
#contact_wrap{
  display: flex;
  justify-content: space-around;
  margin: 0px auto 100px auto;
  width: 1080px;
}
.contact{
  margin: 50px 0;
}
.contact span{
  font-size: 22px;
}
.contact_sub{
  text-indent: 100px;
  line-height: 30px;
  transform: translateY(-10px);
}

.contact_sub li{
  font-size: 18px;
}
.contact_sub li  span, .phone{
  color: #5c5656;
  font-size: 18px;
}
.contact_img{
  position: relative;
  width:300px;
  overflow: hidden;
  animation:myimg 3s infinite;

}
@keyframes myimg{
  0%{
    transform:rotate(7deg);
  }
  50%{
    transform: rotate(-7deg);
  }
  100%{
    transform: rotate(7deg);
  }
}
.contact_img img{
  width: 100%;
}

