*{margin: 0px; padding: 0px;}
ul,ol,li{list-style: none;}
body{
  font-family: "맑은 고딕", Arial, sans-serif;
  background-image: url(../../public/images/bg.jpg) ;
  background-attachment:fixed;
  background-size:cover;
  overflow-x: hidden;

  
}
a{text-decoration: none;}
header{
  height: 100px;
  width: 100%;
}
h1{
  width: 90px;
  margin-left: 40px;
}
h1 img{width: 100%;}

.nav {
  display: flex;
}
.nav ul{
  margin: 0 auto;
  width: 80%;
  height: 40px;
  display: flex;
  justify-content: space-around;
}
.nav > ul > li{
  width: 100%;
  height: 100%;
  padding:10px 40px;
  font-size: 22px;
  font-weight: bold;
}
.mnu_link{
  color: #a98323;
  text-shadow: 0px 0px 2px #ffae00;
}


/* 푸터영역 */
footer{
  background-color: #f6e7c1;
}
footer address{
  font-style: normal;
  text-align: center;
  position: relative;
  bottom: 0px;
  color: #8a6814;
  line-height: 70px;
  font-size: 18px;
}



/* 탑버튼 서식 */
#top_btn_wrap{
  cursor: pointer;
  border-radius: 50%;
  border:solid 1px #928d8d;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  position: fixed;
  right: 30px;bottom: 50px;
  background-color: #ffdc84;
  box-shadow: 0px 2px 3px #b6b6b6;
}
#top_btn{
  cursor: pointer;
  border: none;
  background-color: #ffdc84;
  color: #736B66;
  font-weight: bold;
  font-size: 17px;

}
