@charset "utf-8";

/* project tab서식 */
.is-active{
  font-weight: bold;
  border-bottom: solid 3px #736B66;
  color: #211b09;
}



.tab_cont_wrap, .tab_cont_wrap4{
  display: flex;
  margin: 0 auto;
  width: 1000px;
  height: 600px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.con_wrap{
  display: flex;
  justify-content: center;
}
.con_wrap li{
  cursor: pointer;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  font-size: 18px;  
  color: #736B66;
  padding: 10px 59px;
}

.link_wrap{
  border-radius: 5px;
  background-color: #F2E5BD;
  padding: 5px 10px;
  border: solid 1px #736B66;
  display: block;
  width:120px;
  text-align: center;
}

.btn_wrap{
  display: flex;
  margin-top: 50px;
}
.website_btn{
  color: #736B66;
}
.con_txt{
  width: 600px;
  margin-top: 60px;
  padding: 10px;
  box-sizing: border-box;
  line-height: 35px;
}


.mobile_ofd{
  margin-left: 127px;
}

/* 개인프젝 */
.con_wrap2{
  display: grid;
  width: 1000px;
  padding: 10px;
  grid-template-columns: repeat(2,auto);
  grid-template-rows: repeat(2,280px);
  margin: 0 auto;
}
.con_wrap2 li{
  width: 500px;
  height: 275px;
  overflow: hidden;
}

.con_wrap2 li:hover{
  z-index:100;
  filter: grayscale(1.1);
  transition: .4s;
}

figure{
  position: relative;
}
figcaption{
  position: absolute;
  width: 100%;height: 75px;
  text-align: right;
  background-color: rgba(0, 0, 0, .7);
  color: #fff;
  padding: 5px;
  box-sizing: border-box;
}
.caption01{
  bottom: 60px;
}
.caption02{
  bottom: 30px;
}
.caption02 > .p_title02{
  transform: translate(-75px,-10px);
}
.caption02 >.project_desc{
  transform: translate(-80px,5px);
}
.caption03{
  bottom: 35px;
}
.caption04{
  bottom: 48px;
}
.detail_btn{
  display: inline-block;
  color: #fff;
  border: solid 1px #fff;
  padding: 3px 7px;
  margin-right: 10px;
  transform: translateY(-10px);
}
.d_btn02{
  transform: translate(5px,-40px);
}
.project_title{
  display: inline-block;
  font-size: 20px;
  transform: translateY(-10px);
}
.p_title03{
  transform: translate(-30px,-10px);
}
.p_title04{
  transform: translate(-30px,-10px);
}
.project_desc{
  display: inline-block;
  width: 200px;height: 60px;
  text-justify: auto;
  font-weight: normal;
  text-align: left;  

}
.con_wrap2 img{
  width: 100%;
}
.con_wrap2 img:hover{
  scale: 1.1;
  transition: .3s;

}


.item{
  display: flex;
  width: 1000px;
  justify-content: space-between;
  margin: 60px auto;
  padding-left: 50px;
  padding-bottom: 50px;

}
.cont_img{
  width: 550px;
  overflow: hidden;
}
.img1 img{
  width: 180%;
  height: 180%;
  transform: translate(-220px,-120px);
}
.img2 img{
  width: 160%;
  height: 160%;
  transform: translate(-210px,-120px);
}
.img3 img{
  height: 130%;
  transform: translate(-100px,-80px);
}
.img4 img{
  width: 150%;
  height: 150%;
  transform: translate(-135px,-115px);
}
.con_txt{
  width:40%;
}

